.github-pinned-repo {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    padding: 20px;
    height: 150px;
    width: 400px;
    border-radius: 25px;
    background-color: #f2f2f2;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
    transition: all 0.5s ease-in-out;  
  }
  
  .github-pinned-repo-left {
    width: 50px;
    height: 50px;
    margin-right: 20px;
 
  }
  
  .github-pinned-repo-left img {
    height: 100%;
  }
  
  .github-pinned-repo-right {
    width: 100%;
  }
  
  .github-pinned-repo-right h3 {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: bold;
    color: #333;
  }
  
  .github-pinned-repo-details {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin-bottom: 20px;
  }
  
  .github-pinned-repo-details .language {
    margin-right: 20px;
    color: #555;
  }
  
  .github-pinned-repo-details .stars {
    color: #f1c40f;
  }
  
  .github-pinned-repo-right p {
    font-size: 14px;
    color: #555;
  }

.github-pinned-repo:hover {
    background-color: lightgrey;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Poppins', sans-serif;
}
.about-wrapper{
    height: 100vh;
}
.about-left{
    background: linear-gradient(to right, 
    rgba(96,239,255,0.8),
    rgba(0,97,255,0.8), 
    rgba(50,21,234,0.8)
  );
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.about-left-content > div{
    background: linear-gradient(to bottom, 
    rgba(0,97,255), 
    rgba(96,239,255)
  );
    padding: 4rem 4rem 2.5rem 5rem;
    text-align: center;
}
.about-left-content{
    box-shadow: 0px 0px 18px -1px rgba(0, 0, 0, 0.39);
    -webkit-box-shadow: 0px 0px 18px -1px rgba(0, 0, 0, 0.39);
    -moz-box-shadow: 0px 0px 18px -1px rgba(0, 0, 0, 0.39);
}
.about-img img{
    display: block;
    width: 200px;
}
.about-img{
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    transition: all 0.5s ease-in-out;
}
.shadow{
    margin-left: auto;
    margin-right: auto;
    border-radius: 50%;
    width: 200px;
    height: 200px;
    position: relative;
    cursor: pointer;
}
.shadow::after{
    content: "";
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: -20px;
    height: 7px;
    width: 50px;
    filter: blur(3px);
    -webkit-filter: blur(3px);
    opacity: 0;
    transition: all 0.6s ease;
}
.shadow:hover .about-img{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.shadow:hover::after{
    opacity: 0;
}
.about-left-content h2{
    font-size: 2rem;
    margin: 2.2rem 0 0.6rem 0;
    line-height: 1.2;
    padding-bottom: 1rem;
    border-bottom: 2px solid #edffec;
}
.about-left-content h3{
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 5px;
    margin-top: 1.2rem;
}
.icons{
    background: #fff;
    display: flex;
    justify-content: center;
    padding: 0.8rem 0;
}
.icons a { 
    color: inherit; 
}
 
.icons li{
    list-style-type: none;
    background: #15CDEA;
    color: #fff;
    width: 40px;
    height: 40px;
    margin: 0 0.5rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
.icons li:hover{
    background: #edffec;
    color: #000;
}
.about-right{
    background: #fff;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 5rem;
    text-align: center;
}
.about-right h1{
    font-size: 7rem;
    text-transform: uppercase;
}
.about-right h1 span{
    color: #3215EA;
}
.about-right h2{
    font-weight: 600;
}
.about-btns{
    display: flex;
    margin: 2rem 0;
}
.btn{
    border: none;
    font-size: 0.9rem;
    text-transform: uppercase;
    border: 2px solid #000;
    border-radius: 20px;
    padding: 0.55rem 0;
    width: 130px;
    font-weight: 600;
    background: transparent;
    margin: 0 0.5rem;
    cursor: pointer;
}
.btn.btn-pink{
    background: #3215EA;
    color: #fff;
    border-color: #3215EA;
    transition: all 0.5s ease-in-out;
}
.btn.btn-pink:hover{
    background: transparent;
    border-color: #3215EA;
    color: #000;
}
.btn.btn-white{
    transition: all 0.5s ease-in-out;
}
.btn.btn-white:hover{
    background: #3215EA;
    border-color: #3215EA;
    color: #fff;
}
.about-para p{
    font-weight: 300;
    padding: 0.5rem;
    opacity: 0.8;
}

@media screen and (min-width: 992px){
    .about-wrapper{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .about-left{
        position: relative;
    }
    .about-left-content{
        position: absolute;
        right: -50px;
    }
}
.footer {
    background-color: #343a40;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
  }
  
  .footer-text {
    font-size: 18px;
    font-weight: bold;
  }
